
import React, { Component } from "react";
import MultiSelect from "@kenshooui/react-multi-select";
import { InputGroup, Button, FormControl, Card } from "react-bootstrap";
import axios from "axios"
import Aux from "../../../hoc/_Aux";
import "@kenshooui/react-multi-select/dist/style.css"
import MessageModal from "./../../../Components/Alert/Alert";
import i18n from './../../../i18n';

var urlbackend = require("../../../env/env")();

class ListPickupsAdmin extends Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.state = {
            items: [],
            selectedItems: [],
            drivers: [],
            iddriver: '',
            showMessageParams:{
                display:false,
                type:"success",
                message:null
                
            }
        };
    }
    componentDidMount() {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        var getPendingPack = axios.get(
            urlbackend + "/admin/pickup/pending?current_page=1&per_page=500",
            config
        );


        var getdrivers = axios.get(urlbackend + "/driver", config);

        axios
            .all([getPendingPack, getdrivers])
            .then(
                axios.spread((...response) => {
                    console.log("this respon list0", response[0].data);
                    console.log("this respon list1", response[1].data);
                     this.setState({ selectedItems:[] });
                    this.setState({ drivers: response[1].data.data });
                    this.setState({ items: response[0].data.data.data.filter(e => !e.driver) });

                })
            )
            .catch((errors) => {
                // react on errors.
                console.error(errors);
            });
    }
    //affectation des "driver" par groupe body : {driver:"iddriver" , package:["idpackage", .....]  url "/v3/package/edit/driver" methode "PUT
    savedriver() {
        //
        console.log("driver data=>",{ driver: this.state.iddriver, package: this.state.selectedItems.map(e => e.id) });

        this.setState({ 
            showMessageParams:{
                display:false,
                type:"success",
                message:null
                
            }
        });
        // if(this.state.iddriver==''){
        //     this.setState({ 
        //         showMessageParams:{
        //             display:true,
        //             type:"danger",
        //             message:"Add a driver !"
                    
        //         }
        //     });
        // }

        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        axios.put(
            urlbackend + "/pikup/add/driver",
            { driver: this.state.iddriver, coliss: this.state.selectedItems.map(e => e.id) },
            config
        )
            .then((response) => {
                console.log("response.data.driver", response.data.data.driver);

                if(response.data.data.driver==="" || response.data.data.driver==="Choisir un Chauffeur"){
                    this.setState({ 
                        showMessageParams:{
                            display:true,
                            type:"danger",
                            message:i18n.t('add-driver-mandatory')
                        }
                    });
                }else{
                    this.setState({ 
                        showMessageParams:{
                            display:true,
                            type:"success",
                            message:i18n.t('add-driver-success')
                        }
                    });
                    // alert("add Succefull AAAs");
                    console.log("response.data", response.data);
                    this.componentDidMount();
                    //this.reloaddata()
                }
            })
            .catch((e) => {
                console.log(e);
                this.setState({ 
                    showMessageParams:{
                        display:true,
                        type:"danger",
                        message:i18n.t('error-add-existing-driver')
                    }
                });
            });
    }
    handleChange(selectedItems) {
        this.setState({ selectedItems });
    }
    render() {
        const { items, selectedItems,showMessageParams } = this.state;
        return (
            <Aux>

                <Button onClick={() => this.savedriver()} > {i18n.t('send')}</Button>

                <Card>
                {showMessageParams.display===true ?
                <MessageModal message={showMessageParams.message} design={showMessageParams.type} showit={showMessageParams.display} />
                :null
                }

                    <InputGroup className="mb-3">
                        <FormControl
                            as="select"
                            id="{this.state.driver}"
                            onChange={(e) =>
                                this.setState({ iddriver: e.target.value })
                            }
                            custom
                        >
                            <option>{i18n.t('choose-driver')}</option>

                            {this.state.drivers.map((driver, index) => (
                                <option
                                    className=" badge badge-info"
                                    key={driver.id}
                                    value={driver.id}
                                >
                                    {driver.username}
                                </option>
                            ))}
                        </FormControl>
                    </InputGroup>

                    <MultiSelect
                        items={items.map(e => {
                            return { "id": e.colis.id, label:  e.colis.index_colis + "| "+ e.colis.provider.username + " : " + e.colis.provider.phone_number ,group : e.colis.provider.username}

                        })}
                        selectedItems={selectedItems}
                        onChange={this.handleChange}
                        responsiveHeight= {800}
                        withGrouping
                        
                    />
                </Card>
            </Aux>
        );
    }
}
export default ListPickupsAdmin